<template>
  <div class="PixelFrame">
    <div
      class="frame"
      :style="{width:frameW+'px',height:frameH+'px'}"
      @click="selectFile"
      @dragover="prepareDrop"
      @drop="receiveDrop"
    >
      <form action="fileupload" method="post" enctype="multipart/form-data">
        <input ref="fileInput" type="file" name="filetoupload" @change="fileInputChange" />
        <input type="submit" />
      </form>
      <div v-if="previewDataUrl!=null" class="framePreview">
        <div class="grid-overlay">
          <!-- row -->
          <template v-for="(n, i) in pixelH">
            <!-- col -->
            <div :key="i" class="grid-row">
              <template v-for="(nn, ii) in pixelW ">
                <div :key="'r'+ii" class="grid-cell"></div>
              </template>
            </div>
            <!-- <div :key="index" :style="{width:(frameW-20)/pxWidth+'px',height:(frameW-20)/pxWidth+'px'}" class="grid-cell"></div> -->
          </template>
        </div>
        <img :src="previewDataUrl" alt />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PixelFrame',
  props: ['frameW', 'frameH', 'pixelW', 'pixelH', 'index', 'maxSize'],
  data() {
    return {
      previewDataUrl: null,
    }
  },
  methods: {

    getDataUrl(file) {
      return new Promise((resolve) => {
        var reader = new FileReader();
        reader.onload = (ev) => {
          resolve(ev.target.result);
        }
        reader.readAsDataURL(file);
      })
    },
    prepareDrop(e) {
      e.preventDefault();
    },
    receiveDrop(e) {
      e.preventDefault();
      var file;
      if (e.dataTransfer.items.length > 0) {
        file = e.dataTransfer.items[0].getAsFile();
      } else if (e.dataTransfer.files.length > 0) {
        file = e.dataTransfer.files[0]
      }
      if (file) {
        this.processFile(file);
        // var isValid = this.validateFile(file);
        // if (isValid.result) {
        //   this.getDataUrl(file).then((res) => {
        //     this.previewDataUrl = res;
        //   })
        // } else {
        //   this.rejectFile(isValid.reasons)
        // }

      }


      // this.uploadFile(file);
    },
    selectFile() {
      this.$refs.fileInput.click();
    },
    fileInputChange(e) {
      var file = e.target.files[0];
      this.processFile(file);

      // this.getDataUrl(file).then((res) => {
      //   this.previewDataUrl = res;
      // })


    },
    rejectFile(reasons) {
      alert(reasons)
    },
    validateFile(file) {
      var reasons = [];
      if (file.type != 'image/gif') {
        reasons.push('Only gif is supported.');
      }
      if (file.size > this.maxSize) {
        reasons.push('Max file size is: ' + (this.maxSize / 1000) + 'kb.');
      }
      if (reasons.length == 0) {
        return { result: true }
      } else {
        return { result: false, reasons: reasons }
      }
    },
    uploadFile(file) {
      return new Promise((resolve) => {
        var url = "/upload";
        var xhr = new XMLHttpRequest();
        var formData = new FormData();
        xhr.open('POST', url, true);
        xhr.addEventListener('readystatechange', (e) => {
          if (xhr.readyState == 4 && xhr.status == 200) {
            resolve(JSON.parse(e.target.responseText))
          } else if (xhr.readyState == 4 && xhr.status != 200) {
            console.log('error: ', e)
          }
        });
        formData.append('file', file);
        formData.append('index', this.index);
        xhr.send(formData);
      });
    },
    processFile(file) {
      var isValid = this.validateFile(file);
      if (isValid.result) {
        this.uploadFile(file).then((result) => {
          if (result.success) {
            this.getDataUrl(file).then((res) => {
              this.previewDataUrl = res;
            })
          } else {
            this.rejectFile(result.reasons);
          }
        })

      } else {
        this.rejectFile(isValid.reasons);
      }
    }
  },
  mounted() {
  }
};
</script>

