  cursor: pointer;
  // box-sizing: border-box;
  border: 20px black solid;
  margin-left: 20px;
  margin-right: 20px;
  position: relative;

  .framePreview {
    height: 100%;
    background-color: white;
  }
  .grid-overlay {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    position: absolute;
    z-index: 2;
    .grid-row {
      // border:1px red solid;
      height: 100%;
      flex-basis: 1;
      display: flex;
    }
    .grid-cell {
      box-sizing: border-box;
      height: 100%;
      border: 2px black solid;
      flex-grow: 1;
      transform: scale(1.05);
    }
  }
  img {
    width: 100%;
    height: 100%;
    image-rendering: pixelated;
    opacity: 0.95;
  }
  form {
    display: none;
  }
}
</style>
